<template>
  <div>
    <div class="form">
      <el-form ref="form" label-width="120px" style="margin:30px;width:auto;">
        <h2 style="font-weight:bold; margin-bottom: 10px; font-size:20px">:</h2>
        <el-input
          class="title"
          v-model="page_content.page_name"
          placeholder=""
          required="true"
        ></el-input>
        <!-- editorkeytinymce keep-alivetinymce
        activatedkey-->
        <editor
          id="tinymceEditor"
          :init="tinymceInit"
          v-model="page_content.html_context"
          :key="tinymceFlag"
        ></editor>
        <el-upload
          class="upload-demo"
          ref="imageUpload"
          :action="imageUrl"
          :on-preview="handlePreview"
          :on-remove="handleRemove"
          :file-list="fileList"
          :auto-upload="true"
          multiple
          :on-success="insertImage"
          style="display:none"
        ></el-upload>
        <el-form-item class="text_right">
          <el-button type="primary" @click="onSubmit()"></el-button>
          <el-button @click="onCancel()"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
import tinymce from "tinymce/tinymce";
import "tinymce/themes/silver/theme";
import Editor from "@tinymce/tinymce-vue";

import "tinymce/plugins/textcolor";
import "tinymce/plugins/advlist";
import "tinymce/plugins/table";
import "tinymce/plugins/image";
import "tinymce/plugins/media";
import "tinymce/plugins/link";
import "tinymce/plugins/lists";
import "tinymce/plugins/paste";
import "tinymce/plugins/preview";
import "tinymce/plugins/fullscreen";

export default {
  name: "TinymceEditor",
  components: {
    editor: Editor
  },
  data() {
    return {
      url: "",
      imageUrl: `https://sniweb.shouting.feedia.co/php/PictureUpload.php?sid=${window.$cookies.get("sid")}`,
      tinymceFlag: 1,
      id: "",
      tinymceInit: {},
      page_content: {
        html_context: "",
        id: "",
        page_group_id: "",
        page_name: ""
      },
      fileList: []
    };
  },
  methods: {
    // 
    insertImage(res, file) {
      let src = `https://sniweb.shouting.feedia.co/php/${file.response.url}`; // 
      tinymce.execCommand("mceInsertContent", false, `<img src=${src}>`);
    },
    getPageContent() {
      // id
      this.url = location.href;
      //edit
      if (this.url.indexOf("/navpageedit/edit/") !== -1) {
        //
        const ary1 = this.url.split("/navpageedit/edit/");
        this.id = ary1[ary1.length - 1];
        //
        this.$axios
          .post(
            `https://sniweb.shouting.feedia.co/php/GetContent.php?page_id=${
              this.id
            }`
          )
          .then(res => {
            this.page_content = res.data;
            this.page_content.page_id = res.data.id;
          })
          .catch(err => console.log(err));
      } else {
        this.page_content.page_group_id = this.$route.params.page_group_id;
      }
    },
    onSubmit() {
      const isEdit = this.url.indexOf("/navpageedit/edit/") !== -1;
      const apiType = isEdit ? "EditContent" : "AddContent";
      //
      this.$axios
        .post(
          `https://sniweb.shouting.feedia.co/php/${apiType}.php?sid=${window.$cookies.get("sid")}`,
          JSON.stringify(this.page_content)
        )
        .then(res => {
          if (res.data.status === "Y") {
            //
            this.$message({
              message: res.data.message,
              type: "success"
            });
            this.$emit("update");
            this.$router.push("/navpageedit");
          } else {
            //
            this.$message({
              message: res.data.message,
              type: "error"
            });
          }
        });
    },
    onCancel() {
      this.$router.push("/navpageedit");
    },
    handleRemove(file, fileList) {
    },
    handlePreview(file) {
    },
    handleExceed(files, fileList) {
      this.$message.warning(
        ` 3  ${
          files.length
        }  ${files.length + fileList.length} `
      );
    },
    beforeRemove(file, fileList) {
      return this.$confirm(` ${file.name}`);
    }
  },
  created() {
    this.tinymceInit = {
      skin_url: "/client/dist/tinymce/skins/ui/oxide",
      language_url: `/client/dist/tinymce/langs/zh_CN.js`,
      language: "zh_CN",
      height: document.body.offsetHeight - 300,
      browser_spellcheck: true, // 
      branding: false, // 
      // elementpath: false,  //
      statusbar: false, // 
      paste_data_images: true, // 
      menubar: false, // menu
      // menubar: "insert", // menu
      plugins: "advlist table lists paste preview fullscreen image media link",
      toolbar:
        "fontselect fontsizeselect forecolor backcolor bold italic underline strikethrough link | alignleft aligncenter alignright alignjustify | imageUpload media quicklink h2 h3 blockquote table numlist bullist preview fullscreen",
      images_upload_url: "postAcceptor.php",
      automatic_uploads: false,
      file_browser_callback: function(field_name, url, type, win) {
        win.document.getElementById(field_name).value = "my browser value";
      },
      file_browser_callback_types: "file image media",
      /**
       * tinymce
       * ElementUIUpload,
       */
      setup: editor => {
        editor.ui.registry.addButton("imageUpload", {
          tooltip: "",
          icon: "image",
          onAction: () => {
            let upload = this.$refs.imageUpload.$refs["upload-inner"];
            upload.handleClick();
          }
        });
      }
    };
    this.getPageContent();
  },
  activated() {
    this.tinymceFlag++;
  },
  mounted() {}
};
</script>

<style scoped>
.text_right {
  float: right;
  margin-top: 20px;
}
.title {
  margin-bottom: 2%;
}
</style>
