import axios from 'axios';
import { Message, Loading } from 'element-ui';
import router from './router';

let loading;
function startLoading() {
    loading = Loading.service({
        lock: true,
        text: '...',
        background: 'rgba(0, 0, 0, 0, 7)'
    });
}

function endLoading() {
    loading.close();
}

